import axios from 'axios';

import {
    REGISTER_SUCCESS,
    REGISTER_FAIL
} from './types'

export const register = (username, password, confirmation, email) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
        }
    }

    const body = JSON.stringify({ username, password, confirmation, email })

    try {
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/network/register`)

        if (res.data.error) {
            dispatch({
                type: REGISTER_FAIL
            })
        } else {
            dispatch({
                type: REGISTER_FAIL
            })
        }

    } catch (err) {
        dispatch({
            type: REGISTER_FAIL
        })

    }
}