import {
    REGISTER_SUCCESS,
    REGISTER_FAIL
} from '../actions/types'

const initState = {
    isAuthenticated: null,
    username: '',
    email: ''
}

export default function(state = initState, action) {
    const {type, payload} = action;

    switch(type) {
        case REGISTER_SUCCESS:
            return {
                ...state,
                isAuthenticated: false
            }
        
        case REGISTER_FAIL:
            return state
        default:
            return state
    }
}